
import { Container, Navbar, Nav, NavDropdown } from "react-bootstrap"
import { LinkContainer } from "react-router-bootstrap"
import { useDispatch } from 'react-redux'

export function AdminMenu({email}) {
	
	const dispatch = useDispatch()

	return (
	<Navbar collapseOnSelect expand="sm" bg="light">
        <Container>
          <Navbar.Brand href="https://www.hrci.io/">
            <img
              src="/hrcilogo.svg" width="75" height="30"
              className="d-inline-block align-top"
            />
          </Navbar.Brand>
          <Navbar.Toggle />
          <Navbar.Collapse>
          <Nav className="me-auto">
          	<LinkContainer to="/">
            <Nav.Link >Status</Nav.Link>
            </LinkContainer>
            <LinkContainer to="/users">
            <Nav.Link >Users</Nav.Link>
            </LinkContainer>
          	<LinkContainer to="/result">
            <Nav.Link>Report</Nav.Link>
            </LinkContainer>
          	<LinkContainer to="/qmgmt">
            <Nav.Link>Questions</Nav.Link>
            </LinkContainer>
          	<LinkContainer to="/index">
            <Nav.Link>Index</Nav.Link>
            </LinkContainer>
          	<LinkContainer to="/plan">
            <Nav.Link>Suunnitelmat</Nav.Link>
            </LinkContainer>
          </Nav>
          <Nav>
          	<NavDropdown align="end" title={email} id="userDropdown">
              <NavDropdown.Item onClick={() => dispatch({type: 'password/show'})}>Change Password</NavDropdown.Item>
              <NavDropdown.Divider />
    	      <NavDropdown.Item onClick={() => { window.location.replace("/"); } }>Log out</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          </Navbar.Collapse>
        </Container>
    </Navbar>
	)

}