import { ApiClient } from './client'

export function postReport(token, surveyId, l1Id) {
	const client = ApiClient(token);
	const params = {
		surveyId: surveyId,
		l1Id: l1Id
	}
	return client.post('/wh/report', params)
}

export function postReportDetails(token, surveyId) {
	const client = ApiClient(token);
	const params = { surveyId: surveyId }
	return client.post('/wh/report/details', params)
}
