import React, { useRef, useEffect, useState } from "react"
import { Container, Spinner, Form, Row, Col, Navbar, Nav, Table, Accordion, Badge } from "react-bootstrap"
import { Menu } from "../menu"
import { LoginModal } from '../loginview'
import { useSelector, useDispatch } from 'react-redux'
import { postReport, postReportL1 } from '../actions'
import { DrawXAxis, DrawYAxis, DrawData, RemoveAxis, DrawIndex } from "./graphutil"
import { GraphModal } from './graphmodal'


function SurveyDropdown({selection}) {
	const dispatch = useDispatch()
	const token = useSelector((state) => state.authorization.token)
	const surveyId = useSelector((state) => state.report.surveyId)
	
	const value = surveyId === null ? 0 : surveyId;
	
	return (
	<Form.Select aria-label="Select survey to report" value={value} onChange={evt => dispatch(postReport(token, evt.target.value))}>
		{ surveyId === null  && <option disabled key={0} value={0}>Select</option> }
      	{ Object.entries(selection).map( ([key, value]) => { 
			return (<option key={key} value={key}>{value}</option>);
   		})}
    </Form.Select>
    );
}

function L1ReportTable() {
	const l1result = useSelector((state) => state.report.l1result)
	
	if(l1result === null) {
		return <tr><td><Spinner animation="grow" /></td></tr> 
	} else {
		return (
			l1result.map( (entry) =>{
				return (
					<tr key={entry.id} >
						<td key={0} style={{'textAlign':'left'}}>{entry.title}</td><td style={{'textAlign':'left'}}>(n={entry.n-entry.zeroes}/{entry.n})</td>
						{entry.quartiles.map((value, i) => {
							return <td key={i}>{value}</td>
						})}
					</tr>
				)
		})
		);
	}
}

function SurveyReportTable() {

	const dispatch = useDispatch()
	const token = useSelector((state) => state.authorization.token)
	const surveyId = useSelector((state) => state.report.surveyId)
	const result = useSelector((state) => state.report.result)
	const l1Id = useSelector((state) => state.report.l1Id)
	
	return (
		<Accordion flush activeKey={l1Id} onSelect={(key) => dispatch(postReportL1(token, surveyId, key)) }>
			{result.map( (entry) =>{
				return (
					<Accordion.Item key={entry.id} eventKey={entry.id}>
					<Accordion.Header>{entry.title}<Badge pill bg="dark" className="ms-auto me-1">{entry.quartiles[2]}</Badge></Accordion.Header>
					<Accordion.Body>
						<Table className='report-table' style={{'fontSize':'12px', 'padding': '0.5rem 0'}}>
						<tbody>
						{ entry.id === l1Id && <L1ReportTable /> }
						</tbody>
						<tfoot>
						<tr key={0} ><th/><th style={{'textAlign':'left'}}>(n={entry.n-entry.zeroes}/{entry.n})</th>
						{entry.quartiles.map((value, i) => {
							return <th key={i}>{value}</th>
						})}
						</tr>
						</tfoot>
					</Table>
					</Accordion.Body>
					</Accordion.Item >
				)
			})}
		</Accordion>
	);
}


function SurveyReport() {

	const surveyId = useSelector((state) => state.report.surveyId)
	const result = useSelector((state) => state.report.result)
	
	const loading = result === null && surveyId !== null
	
	return (
		<>
		{ loading && <Spinner animation="grow" /> }
		{ result !== null && <SurveyReportTable /> }
		</>
	)
}

function SurveyGraph({onClick}) {

  	const svgRef = useRef(null);

	const result = useSelector((state) => state.report.result)
	const average = useSelector((state) => state.report.average)
  	
	useEffect(() => {
		/* console.log('SurveyGraph.useEffect', result) */
		if(result === null) {
			RemoveAxis(svgRef);
		} else {
			DrawXAxis(svgRef, 'result', result, 50, 150);
			DrawYAxis(svgRef, result, 50, 150);
			if(result !== null && result.length > 0) {
				DrawData(svgRef, result, 50, 150);
			} 
			DrawIndex(svgRef, 25, average);
		}
	}, [result])

	if(result) {
	return (
		<>
		<svg className="d-block mx-auto" viewBox="-175,-175,350,350" strokeLinejoin="round" strokeLinecap="round" ref={svgRef} style={{ background: "white" }} onClick={onClick} >
			<g className="xaxis"/>
			<g className="yaxis"/>
			<g className="index"/>
			<g className="data" />
   		</svg>
		</>
	)} else {
		return null;
	}
}


export default function ResultView() {

	const dispatch = useDispatch()
	const token = useSelector((state) => state.authorization.token)
	const selection = useSelector((state) => state.report.selection)
	
	const [show, setShow] = useState(false)

	useEffect(() => {
		if(token !== null) {
			dispatch(postReport(token, null))
		}
	}, [token])

	return (
	<>
		<Menu />
		<LoginModal />
		<GraphModal show={show} onClose={() => setShow(false)} />
		<Container>
			<Row><h4>Tulokset</h4></Row>
		{ !selection && <Spinner animation="grow" /> }
		{ selection  && 
		<>
			<Row><Col><SurveyDropdown selection={selection} /></Col></Row>
			<Row>
			<Col className="mt-2" md={6} >
			<SurveyReport />
			</Col>
			<Col className="mt-2" md={6}>
			<SurveyGraph onClick={() => setShow(true)} />
			</Col>
			</Row>
		</>
		}
		</Container>
	</>
	);

}

