import { ApiClient } from './client'

export function getQuestion(token, url) {
	const params = {
		url: url
	}
	const client = ApiClient(token);
	return client.post('/', params);
}

export function postAnswer(token, url, answerId, result) {
	const params = {
		url: url,
		answerId: answerId,
		result: result
	};

	const client = ApiClient(token);
	return client.post('/', params);
}

