import * as api from '../api/'

export const postReport = (token, surveyId) => {
	return dispatch => {
		dispatch({ type: 'report/select', payload: surveyId });
		api.postReport(token, surveyId, null)
		.then(resp => {
			dispatch({
				type: 'report/store',
				payload: resp.data
			});
		})
		.catch(e => console.log(e));
	}
} 

