import react, { useEffect } from 'react'
import { Container, Spinner } from "react-bootstrap";
import { useSelector, useDispatch } from 'react-redux'
import { fetchQuestion } from '../actions'
import { useParams } from 'react-router-dom'

import QEntry from './qentry'
import ThankYou from './thankyou'
import { LoginModal } from '../loginview'

export default function Question() {

	const params = useParams()	
	const question = useSelector((state) => state.question.question)
	const error = useSelector((state) => state.question.error)
	const token = useSelector((state) => state.authorization.token)
	const dispatch = useDispatch()
	
	useEffect(() => {
		dispatch({type:'authorization/login', payload: { token: params.url, scope: 'survey' }})
	}, [])
	
	useEffect(() => {
		if(token != null) {
			dispatch(fetchQuestion(token, params.url))
		}
	}, [token])

	const spin = question == null && error == null

	return(
	<>
	  <LoginModal />
	  { token && 
      <Container>
      	<div className="py-5 text-center">
      	<img className="d-block mx-auto mb-4" src="/hrcilogo.svg" width="188" height="75"/>
      	{ spin && <Spinner animation="grow" />}
      	{ question !== null && <h2>{question.l1title}</h2> }
      	{ error !== null && <h1>{error}</h1> }
      	</div>
      	{ question !== null && question.answerId != 0 && <QEntry /> }
      	{ question !== null && question.answerId == 0 && <ThankYou /> }
      </Container>
      }
     </>
	);
}