import react from 'react'
import { Card, Button, Form } from "react-bootstrap";
import { useSelector, useDispatch } from 'react-redux'
import { result } from '../reducers/questionslice'
import { postAnswer } from '../actions'


function languageNA(language) {
	switch(language) {
		case 'et':
			return 'Ma ei saa öelda';
		case 'lv':
			return 'Es nevaru pateikt';
		case 'lt':
			return 'Negaliu pasakyti';
		default:
			return 'En osaa sanoa';
	}
}

function languageNext(language) {
	switch(language) {
		case 'et':
			return 'OK';
		case 'lv':
			return 'OK';
		case 'lt':
			return 'OK';
		default:
			return 'Seuraava';
	}
	
}

export default function QEntry() {

	const question = useSelector((state) => state.question.question)
	const url = question.url
	const answerId = question.answerId
	const dontknow = languageNA(question.language)
	const next = languageNext(question.language)
	const l2title = question.l2title
	const title = question.title
	const resultValue = question.result
	const token = useSelector((state) => state.authorization.token)
	const dispatch = useDispatch()

	const onClick = x => {
		dispatch(result(x));
	}
	
	return (
		<Card>
          <Card.Body>
            <Card.Title>{l2title}</Card.Title>
            <Card.Text>{title}</Card.Text>
            <Form.Check inline type='radio' onChange={() => onClick(0)} name='group' value={0} checked={resultValue==0} id='radio-0' label={dontknow} />
            <Form.Check inline type='radio' onChange={() => onClick(1)} name='group' value={1} checked={resultValue==1} id='radio-1' label='1' />
            <Form.Check inline type='radio' onChange={() => onClick(2)} name='group' value={2} checked={resultValue==2} id='radio-2' label='2' />
            <Form.Check inline type='radio' onChange={() => onClick(3)} name='group' value={3} checked={resultValue==3} id='radio-3' label='3' />
            <Form.Check inline type='radio' onChange={() => onClick(4)} name='group' value={4} checked={resultValue==4} id='radio-4' label='4' />
            <Form.Check inline type='radio' onChange={() => onClick(5)} name='group' value={5} checked={resultValue==5} id='radio-5' label='5' />
            <Form.Check inline type='radio' onChange={() => onClick(6)} name='group' value={6} checked={resultValue==6} id='radio-6' label='6' />
            <Form.Check inline type='radio' onChange={() => onClick(7)} name='group' value={7} checked={resultValue==7} id='radio-7' label='7' />
            <Form.Check inline type='radio' onChange={() => onClick(8)} name='group' value={8} checked={resultValue==8} id='radio-8' label='8' />
            <Form.Check inline type='radio' onChange={() => onClick(9)} name='group' value={9} checked={resultValue==9} id='radio-9' label='9' />
            <Form.Check inline type='radio' onChange={() => onClick(10)} name='group' value={10} checked={resultValue==10} id='radio-10' label='10' />
          </Card.Body>
          <Card.Footer>
            <Button className="w-100" disabled={resultValue==null} onClick={() => dispatch(postAnswer(token, url, answerId, resultValue))}>{next}</Button>
          </Card.Footer>
        </Card>
	);
	
}